

h4 {
  text-transform: uppercase;
}

.classic {
  width: 100vw;
}

.headers {
  display: grid;
  text-align: center;
}

#mono {
  display: flex;
  justify-content: center;
}

.size {
  text-align: center;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.name {
  padding: 5px;
  display: grid;
  grid-template-columns: 50% 50%;
}

.table {
  padding: 5px;
  display: grid;
  grid-template-columns: 50% 50%;
  text-align: center;
}

.tbname {
  text-align: left;
}
