* {
  margin: 0;
  color:white;
  font-weight: bold;
}

.block {
  position: absolute;
  width: 100vw;
  height: 100vw;
  background-color: #D14841;
  visibility: hidden;
}

@media only screen and (min-width: 768px) {
  .block {
    position: absolute;
    width: 100vw;
    height: 100vw;
    background-color: #D14841;
    visibility: visible;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 100px;
  }
}

.app {
  display: grid;
}
